"use client"

import { useState } from "react"
import { Card, CardHeader, CardTitle, CardContent } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Switch } from "@/components/ui/switch"
import Image from "next/image"
import { Settings, User, Bell, Shield } from "lucide-react"

/**
 * Radix Themes Component
 * 
 * Implements Radix Themes Community design patterns from Figma frame 58764-4114.
 * This component follows Radix Themes design system principles:
 * - Clean, modern interface
 * - Proper spacing and typography
 * - Accessible form controls
 * - Consistent with Radix UI patterns
 */
export function RadixThemesComponent() {
  const [settings, setSettings] = useState({
    notifications: true,
    darkMode: false,
    autoSave: true,
  })

  const [formData, setFormData] = useState({
    name: "",
    email: "",
  })

  return (
    <Card className="overflow-hidden h-full flex flex-col">
      {/* Header Section with Icon */}
      <CardHeader className="pb-4">
        <div className="flex items-center gap-3 mb-2">
          <div className="p-2 rounded-lg bg-[var(--color-primary)]/20">
            <Settings className="w-5 h-5 text-[var(--color-primary)]" />
          </div>
          <div className="flex-1">
            <CardTitle className="text-xl font-semibold text-white">
              Settings
            </CardTitle>
            <p className="text-sm text-white/60 mt-1">
              Manage your preferences
            </p>
          </div>
        </div>
      </CardHeader>

      <CardContent className="space-y-6 flex-1 flex flex-col">
        {/* Profile Section */}
        <div className="space-y-4">
          <div className="flex items-center gap-2 pb-2 border-b border-white/10">
            <User className="w-4 h-4 text-white/60" />
            <h3 className="text-sm font-semibold text-white">Profile</h3>
          </div>

          <div className="space-y-3">
            <div className="space-y-2">
              <Label htmlFor="name" className="text-white text-sm">
                Name
              </Label>
              <Input
                id="name"
                placeholder="Enter your name"
                value={formData.name}
                onChange={(e) =>
                  setFormData({ ...formData, name: e.target.value })
                }
                className="bg-slate-800/70 border-white/10 text-white placeholder:text-white/40 focus:border-[var(--color-primary)]"
              />
            </div>

            <div className="space-y-2">
              <Label htmlFor="email" className="text-white text-sm">
                Email
              </Label>
              <Input
                id="email"
                type="email"
                placeholder="Enter your email"
                value={formData.email}
                onChange={(e) =>
                  setFormData({ ...formData, email: e.target.value })
                }
                className="bg-slate-800/70 border-white/10 text-white placeholder:text-white/40 focus:border-[var(--color-primary)]"
              />
            </div>
          </div>
        </div>

        {/* Preferences Section */}
        <div className="space-y-4">
          <div className="flex items-center gap-2 pb-2 border-b border-white/10">
            <Bell className="w-4 h-4 text-white/60" />
            <h3 className="text-sm font-semibold text-white">Preferences</h3>
          </div>

          <div className="space-y-4">
            <div className="flex items-center justify-between">
              <div className="flex-1">
                <Label htmlFor="notifications" className="text-white text-sm">
                  Notifications
                </Label>
                <p className="text-xs text-white/50 mt-0.5">
                  Receive email notifications
                </p>
              </div>
              <Switch
                id="notifications"
                checked={settings.notifications}
                onCheckedChange={(checked) =>
                  setSettings({ ...settings, notifications: checked })
                }
              />
            </div>

            <div className="flex items-center justify-between">
              <div className="flex-1">
                <Label htmlFor="darkMode" className="text-white text-sm">
                  Dark Mode
                </Label>
                <p className="text-xs text-white/50 mt-0.5">
                  Use dark theme
                </p>
              </div>
              <Switch
                id="darkMode"
                checked={settings.darkMode}
                onCheckedChange={(checked) =>
                  setSettings({ ...settings, darkMode: checked })
                }
              />
            </div>

            <div className="flex items-center justify-between">
              <div className="flex-1">
                <Label htmlFor="autoSave" className="text-white text-sm">
                  Auto Save
                </Label>
                <p className="text-xs text-white/50 mt-0.5">
                  Automatically save changes
                </p>
              </div>
              <Switch
                id="autoSave"
                checked={settings.autoSave}
                onCheckedChange={(checked) =>
                  setSettings({ ...settings, autoSave: checked })
                }
              />
            </div>
          </div>
        </div>

        {/* Optional: Image Section (uncomment when image is available) */}
        {false && (
          <div className="relative w-full h-32 rounded-lg overflow-hidden">
            <Image
              src="/images/radix-theme-illustration.jpg"
              alt="Radix Theme illustration"
              fill
              className="object-cover"
              sizes="(max-width: 768px) 100vw, (max-width: 1200px) 50vw, 33vw"
            />
          </div>
        )}

        {/* Actions */}
        <div className="flex gap-3 pt-4 border-t border-white/10">
          <Button variant="outline" className="flex-1">
            Cancel
          </Button>
          <Button className="flex-1 bg-[var(--color-primary)] hover:opacity-90">
            Save Changes
          </Button>
        </div>
      </CardContent>
    </Card>
  )
}

